@import '../common.css';

.menu {
  background-color: #333;
  padding: 20px 0;
}

.menu ul {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.menu li {
  margin: 0 20px;
}

.menu a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  position: relative;
  padding: 5px 0;
  transition: color 0.3s;
}

.menu a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}

.menu a:hover::before {
  visibility: visible;
  transform: scaleX(1);
}

.menu a:hover {
  color: #ddd;
}